import createCache from "@emotion/cache";
import { CacheProvider } from "@emotion/react";
import { Send } from "@mui/icons-material";
import {
  ThemeProvider,
  createTheme,
  CssBaseline,
  useMediaQuery,
  Direction,
  Divider,
  Fab,
} from "@mui/material";
import { SnackbarProvider } from "notistack";
import { useEffect, useMemo, useState } from "react";
import { useTranslation } from "react-i18next";
import { Outlet } from "react-router-dom";
import { prefixer } from "stylis";
import rtlPlugin from "stylis-plugin-rtl";
import { CustomAppBar } from "./components/CustomAppBar";
import { CustomMobileAppBar } from "./components/CustomMobileAppBar";
import { Footer } from "./components/Footer";
import { getLanguageShortForm } from "./components/utils";

const cacheRTL = createCache({
  key: "muirtl",
  stylisPlugins: [prefixer, rtlPlugin],
});

const cacheLTR = createCache({
  key: "muiltr",
});

function getTheme(isDark: boolean, direction: Direction) {
  return createTheme({
    palette: {
      mode: isDark === true ? "dark" : "light",
    },
    direction,
  });
}

export function App(): JSX.Element {
  const {
    i18n: { language, dir, changeLanguage },
  } = useTranslation();

  useEffect(() => {
    document.dir = dir(language);
  }, [dir, language]);

  const [darkMode, setDarkMode] = useState(false);
  const theme = useMemo(() => {
    const docDir = dir(language);
    return getTheme(darkMode, docDir);
  }, [darkMode, language, dir]);

  const matches = useMediaQuery(theme.breakpoints.down("lg"));

  return (
    <CacheProvider value={dir(language) === "rtl" ? cacheRTL : cacheLTR}>
      <ThemeProvider theme={theme}>
        <SnackbarProvider>
          <CssBaseline />
          {matches === true ? (
            <CustomMobileAppBar
              darkMode={darkMode}
              setDarkMode={setDarkMode}
              language={getLanguageShortForm(language)}
              changeLanguage={changeLanguage}
            />
          ) : (
            <CustomAppBar
              darkMode={darkMode}
              setDarkMode={setDarkMode}
              language={getLanguageShortForm(language)}
              changeLanguage={changeLanguage}
            />
          )}
          <Divider sx={{ margin: 2 }} />
          <Outlet />
          <Divider sx={{ margin: 2 }} />
          <Footer />
          <Divider sx={{ margin: 2 }} />
          <Fab
            color="primary"
            aria-label="Send message"
            size="medium"
            sx={{ position: "fixed", right: "10px", bottom: "10px" }}
          >
            <Send />
          </Fab>
        </SnackbarProvider>
      </ThemeProvider>
    </CacheProvider>
  );
}
